<template>
  <div v-highlight class="content" v-html="compiledMarkdown">
  </div>
</template>

<script>
import { get } from '@/api/article'
import marked from 'marked'


 marked.setOptions({
    renderer: new marked.Renderer(),
    gfm: true,
    tables: true,
    breaks: false,
    pedantic: false,
    sanitize: false,
    smartLists: true,
    smartypants: false,
    highlight: function (code, lang) {
        /*if (lang && hljs.getLanguage(lang)) {
            return hljs.highlight(lang, code, true).value;
          } else {
            return hljs.highlightAuto(code).value;
          }*/

        return code
      }
  });


export default {
  name: 'detail',
  data () {
    return {
      html: ''
    }
  },
  watch:{
    $route: {
      handler: function(to, from) {
         if(to.name === from.name) {
            this.reload()
         }
      },
      deep: true
    }
  },
  created() {
    this.reload()
  },
  methods: {
    reload(){
      var id = this.$route.params.id; // 文章唯一标识
      get(id).then(response => {
        this.html = response; //.replace(/\n/g,"\\n").replace(/\r/g,"\\r");
      })
    }
  },
  computed: {
    compiledMarkdown() {
      return marked(this.html || '', {
        sanitize: true
      });
    }
  }
}
</script>

<style scoped>

h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.content{
  text-align: -webkit-auto;
  margin: 0 auto;
  font-size: 100%;
   /* white-space: pre-wrap; */
  width: 60%;
}

</style>



